import HeaderAuthorization from "./authorization";
export default {

    getDataFromGoogleSheet(planId, key) {
        return fetch('https://sheets.googleapis.com/v4/spreadsheets/' + planId + '/values/Plan!A1:C500?key=' + key,
            {})
    },

    getListMileStoneClosed(value) {
        return fetch('https://gitlab.com/api/v4/projects/' + value.projectId + '/milestones?direction=desc&state=closed&page=0&per_page=2000', {
            method: 'get',
            headers: HeaderAuthorization.getHeader()
        })
    },

    getIssueList(projectId) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/issues?page=0&per_page=2000&state=opened', {
            method: 'get',
            headers: HeaderAuthorization.getHeader()
        });
    },

    createMilestone(projectId, dataCreate) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/milestones', {
            method: 'post',
            headers: HeaderAuthorization.getHeader(),
            body: JSON.stringify(dataCreate)
        });
    },

    updateIssueToMilestone(iid, projectId, milestone) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/issues/' + iid, {
            method: 'PUT',
            headers: HeaderAuthorization.getHeader(),
            body: JSON.stringify({
                "milestone_id": milestone
            })
        })
    },

    createIssue(projectId, title, labels, body) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/issues', {
            method: 'post',
            headers: HeaderAuthorization.getHeader(),
            body: JSON.stringify({
                // eslint-disable-next-line no-useless-concat
                title: title,
                labels: labels,
                // eslint-disable-next-line no-useless-concat
                description: body
            })
        })
    },

    getIssueFromMileStone(projectId, mileStoneTitle) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/issues?page=0&per_page=2000&milestone=' + mileStoneTitle, {
            method: 'get',
            headers: HeaderAuthorization.getHeader()
        })
    },

    closeMileStone(projectId, mileStoneId) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/milestones/' + mileStoneId, {
            method: 'PUT',
            headers: HeaderAuthorization.getHeader(),
            body: JSON.stringify({
                "state_event": "close"
            })
        });
    },

    release(projectId, tagName, branch, version, releaseNote, milestones) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/releases', {
            method: 'POST',
            headers: HeaderAuthorization.getHeader(),
            body: JSON.stringify({
                "tag_name": tagName,
                "ref": branch,
                "name": version,
                // eslint-disable-next-line no-useless-concat
                "description": releaseNote,
                "milestones": milestones
            })
        })
    },

    closeIssue(projectId, issueIid) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/issues/' + issueIid, {
            method: 'PUT',
            headers: HeaderAuthorization.getHeader(),
            body: JSON.stringify({
                "state_event": "close"
            })
        })
    },

    getListMileStone(projectId) {
        return fetch('https://gitlab.com/api/v4/projects/' + projectId + '/milestones?page=0&per_page=2000&state=active', {
            method: 'get',
            headers: HeaderAuthorization.getHeader()
        })
    },

    checkLoginToken(authorizationKey) {
        // Fetch to product team
        return fetch('https://gitlab.com/api/v4/groups/7663926', {
            method: 'get',
            headers: new Headers({
                'PRIVATE-TOKEN': authorizationKey
            })
        })
    }
}